import React from 'react'
import {Link} from 'gatsby'

const StartPartners = ({addClass}) => {
	return (
		<section className={`start-partners ${addClass} pt50 pb50`}>
  			<div className="container">
    			<div className="row">
      				<div className="col-lg-8">
        				<div className="start_partner tac-smd">
          					<h2>Become a Real Estate Agent</h2>
          					<p>We only work with the best companies around the globe</p>
        				</div>
      				</div>
      				<div className="col-lg-4">
        				<div className={`parner_reg_btn  ${addClass} text-right tac-smd`}>
          					<Link className="btn btn-thm2" to="/register/">Register Now</Link>
        				</div>
      				</div>
    			</div>
  			</div>
		</section>
	)
}

export default StartPartners