import React, {useState, useEffect} from 'react'
import {graphql, useStaticQuery} from 'gatsby'
import Loadable from '@loadable/component'

const OwlCarousel = Loadable(() => import('react-owl-carousel3'))

const options = {
	loop:true,
	margin:15,
	dots:true,
	nav:false,
	autoplayHoverPause:false,
	autoplay: true,
	smartSpeed: 1200,
	navText: [
	  '<i className="fa fa-arrow-left"></i>',
	  '<i className="fa fa-arrow-right"></i>'
	],
	responsive: {
		0: {
			items: 1,
			center: false
		},
		480:{
			items:1,
			center: false
		},
		600: {
			items: 1,
			center: false
		},
		768: {
			items: 1
		},
		992: {
			items: 1
		},
		1200: {
			items: 1
		}
	}
}
const Testimonials = () => {
	const [display, setDisplay] = useState(false);
	
	const data = useStaticQuery(graphql`
	query
		{
			testimonial: file(relativePath: {eq: "testimonial/1.jpg"}) {
				id
				childImageSharp {
				  fluid {
					originalImg
				  }
				}
			}
		}`)
    useEffect(() => {
        setDisplay(true);
    }, [])

	return (
        
		<section className="our-testimonials">
		<div className="container">
			<div className="row">
				<div className="col-lg-6 offset-lg-3">
					<div className="main-title text-center mb20">
						<h2>Testimonials</h2>
						<p>Here could be a nice sub title</p>
					</div>
				</div>
			</div>
				<div className="row">
	  				<div className="col-lg-6 offset-lg-3">
						{display ? <OwlCarousel className="testimonial_grid_slider style2 owl-carousel owl-theme" {...options}>
                            <div className="item">
                                <div className="testimonial_grid style2">
                                    <div className="thumb">
                                        <img src={data.testimonial.childImageSharp.fluid.originalImg} alt="1.jpg" />
                                        <div className="tg_quote"><span className="fa fa-quote-left"></span></div>
                                    </div>
                                    <div className="details">
                                        <h4>Augusta Silva</h4>
                                        <p>Sales Manager</p>
                                        <p className="mt25">Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                                    </div>
                                </div>
                            </div>
                            <div className="item">
                                <div className="testimonial_grid style2">
                                    <div className="thumb">
                                        <img src={data.testimonial.childImageSharp.fluid.originalImg} alt="1.jpg" />
                                        <div className="tg_quote"><span className="fa fa-quote-left"></span></div>
                                    </div>
                                    <div className="details">
                                        <h4>Augusta Silva</h4>
                                        <p>Sales Manager</p>
                                        <p className="mt25">Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                                    </div>
                                </div>
                            </div>
                            <div className="item">
                                <div className="testimonial_grid style2">
                                    <div className="thumb">
                                        <img src={data.testimonial.childImageSharp.fluid.originalImg} alt="1.jpg" />
                                        <div className="tg_quote"><span className="fa fa-quote-left"></span></div>
                                    </div>
                                    <div className="details">
                                        <h4>Augusta Silva</h4>
                                        <p>Sales Manager</p>
                                        <p className="mt25">Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                                    </div>
                                </div>
                            </div>
                            <div className="item">
                                <div className="testimonial_grid style2">
                                    <div className="thumb">
                                        <img src={data.testimonial.childImageSharp.fluid.originalImg} alt="1.jpg" />   
                                        <div className="tg_quote"><span className="fa fa-quote-left"></span></div>
                                    </div>
                                    <div className="details">
                                        <h4>Augusta Silva</h4>
                                        <p>Sales Manager</p>
                                        <p className="mt25">Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                                    </div>
                                </div>
                            </div>
                            <div className="item">
                                <div className="testimonial_grid style2">
                                    <div className="thumb">
                                    <img src={data.testimonial.childImageSharp.fluid.originalImg} alt="1.jpg" />
                                        <div className="tg_quote"><span className="fa fa-quote-left"></span></div>
                                    </div>
                                    <div className="details">
                                        <h4>Augusta Silva</h4>
                                        <p>Sales Manager</p>
                                        <p className="mt25">Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                                    </div>
                                </div>
                            </div>

						</OwlCarousel> : ''}
	  				</div>
				</div>
  			</div>
		</section>
	)
}

export default Testimonials