import React,{useEffect} from 'react'
import { Link,useStaticQuery, graphql } from 'gatsby'


function HotOfferHomeSeven() {
    
    useEffect(() => {
    	let currentPosition = window.scrollY;
        const parallax= document.getElementById("parallax")
        document.addEventListener("scroll" , ()=>{
            currentPosition = window.scrollY;
            currentPosition>3300 ?
            parallax.style.backgroundPosition="50% "+ (currentPosition-3300)/4 +"px"
            : parallax.style.backgroundPosition="50% "+ 0+"px"
        })
        
    })
    const data = useStaticQuery(graphql`
        query{
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)
    return (
        <section className="our-hot-offer parallax" id="parallax" data-stellar-background-ratio="0.3">
		<div className="container">
			<div className="row mt40">
				<div className="col-md-6 col-lg-4">
					<div className="our_hotoffer">
						<h2>Hot This Week</h2>
						<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
						<Link  to="/list">
						<span className="btn ht_offer">View All</span></Link>
					</div>
				</div>
				<div className="col-md-6 col-lg-4">
					<div className="feat_property home7 style2">
						<div className="thumb">
							<img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp2.jpg"/>
							<div className="thmb_cntnt">
								<ul className="tag mb0">
									<li className="list-inline-item"><Link to="#">For Rent</Link></li>
									<li className="list-inline-item dn"></li>
								</ul>
								<ul className="icon mb0">
									<li className="list-inline-item"><Link to="#"><span className="flaticon-transfer-1"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-heart"></span></Link></li>
								</ul>
								<Link className="fp_price" to="#">$13,000<small>/mo</small></Link>
							</div>
						</div>
						<div className="details">
							<div className="tc_content">
								<p className="text-thm">Villa</p>
								<h4>Gorgeous Villa Bay View</h4>
								<p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
								<ul className="prop_details mb0">
									<li className="list-inline-item"><Link to="#">Beds: 4</Link></li>
									<li className="list-inline-item"><Link to="#">Baths: 2</Link></li>
									<li className="list-inline-item"><Link to="#">Sq Ft: 5280</Link></li>
								</ul>
							</div>
						</div>
					</div>
				</div>
				<div className="col-md-6 col-lg-4">
					<div className="feat_property home7 style2">
						<div className="thumb">
							<img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
							<div className="thmb_cntnt">
								<ul className="tag mb0">
									<li className="list-inline-item"><Link to="#">For Sale</Link></li>
									<li className="list-inline-item dn"></li>
								</ul>
								<ul className="icon mb0">
									<li className="list-inline-item"><Link to="#"><span className="flaticon-transfer-1"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-heart"></span></Link></li>
								</ul>
								<Link className="fp_price" to="#">$13,000<small>/mo</small></Link>
							</div>
						</div>
						<div className="details">
							<div className="tc_content">
								<p className="text-thm">Single Family Home</p>
								<h4>Luxury Family Home</h4>
								<p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
								<ul className="prop_details mb0">
									<li className="list-inline-item"><Link to="#">Beds: 4</Link></li>
									<li className="list-inline-item"><Link to="#">Baths: 2</Link></li>
									<li className="list-inline-item"><Link to="#">Sq Ft: 5280</Link></li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
    )
}

export default HotOfferHomeSeven
